import React from 'react';

function HowWeHelpCard(props) {
    return (
        <div className=' bg-lightgray hover:bg-mediumgray w-full lg:w-2/6 rounded-lg text-center py-16 text-darkgray'>
            <div className='w-9 mx-auto'>
                {props.logo}
            </div>
            <p className='mt-3 text-xl'>
                {props.text}
            </p>
        </div>
    );
}

export default HowWeHelpCard;