import React, {useState} from 'react';
import { Link } from 'react-router-dom'
import { MenuAlt3Icon, XIcon } from '@heroicons/react/solid';

function Navbar() {
    const [showNavbar, setShowNavbar] = useState(false)

    const toggleNavbar = () => {
        setShowNavbar(!showNavbar);
    }

    return (
        <div>
            <div className='flex justify-between mt-10 font-quicksand'>
                <span className='text-2xl'>T2 Codes</span>
                <ul className='hidden md:flex flex-row gap-20'>
                    <li className='my-auto'>
                        <Link to='/support' className=" hover:text-rose">Support</Link>
                    </li>
                    <li className='my-auto'>
                        <Link to='/login' className=" hover:text-rose">Login</Link>
                    </li>
                    <li className='my-auto'>
                        <Link to='/register' className=" hover:text-rose">Register</Link>
                    </li>
                </ul>
                <button 
                    className='md:hidden  hover:text-rose h-6 w-6 cursor-pointer'
                    onClick={toggleNavbar}
                >
                    { showNavbar ? <XIcon /> : <MenuAlt3Icon/>}
                </button>
            </div>
            <ul className={
                    (showNavbar ? 'flex' : 'hidden') + ' flex-row md:hidden' + 
                    ' bg-lightgray mt-10 justify justify-between px-10 py-3 text-sm'}
            >
                <li>
                    <Link to='/support' className=" hover:text-rose">Support</Link>
                </li>
                <li>
                    <Link to='/login' className=" hover:text-rose">Login</Link>
                </li>
                <li>
                    <Link to='/register' className=" hover:text-rose">Register</Link>
                </li>
            </ul>
        </div>
    );
}

export default Navbar;