import React from 'react';
import { Link } from 'react-router-dom';

function Hero() {
    return (
        <div className='flex flex-col-reverse gap-x-12 lg:flex-row lg:mt-16'>
            <div className='flex align-middle lg:w-3/6 text-center lg:text-left'>
                <div className='my-auto'>
                    <h1 className='mt-5 text-4xl lg:text-5xl xl:text-[3.9rem] leading-tight lg:leading-[1.3] capitalize'>
                        Expert Solutions To Your Website And Mobile App Needs
                    </h1>
                    <p className='mt-5 font-quicksand text-xl leading-8'>
                        T2 Codes develops software for its clients to help them market 
                        themselves and their brand efficiently
                    </p>
                    <Link to='register'>
                        <button className='mt-5 bg-rose hover:bg-darkrose text-white px-10 py-2 rounded-full'>
                            Get Started
                        </button>
                    </Link>
                </div>
            </div>
            <div className="w-full lg:w-3/6">
                <img src={require('./../assets/hero.png')} className="sm:w-4/6 lg:w-full mx-auto" />
            </div>
        </div>
    );
}

export default Hero;