import React from 'react';
import { Link } from 'react-router-dom';
import { CookieConsent } from 'react-cookie-consent';

function ConfirmCookies() {
    return (
        <CookieConsent
            disableStyles={true}
            containerClasses='fixed bottom-0 w-full bg-darkgreen text-white flex flex-col lg:flex-row justify-between gap-y-5 gap-x-12 py-7 px-5 lg:px-32'
            buttonClasses='bg-white hover:bg-lightgray text-black rounded-sm py-2 w-32 my-auto'
            buttonWrapperClasses='flex'
            buttonText='Accept'
            expires={365}
        >
            <h3 className='text-lg font-bold'>
                We Value Your Privacy
            </h3>
            <p className='text-sm mt-2 tracking-wide'>
                This website uses technology to collect website performance and usage data to provide better experiences and content. This may include showing you more relevant advertisements. By clicking "Accept", you agree to this. You can learn more via our <Link className='underline' to='/login'>privacy policy</Link>.
            </p>
        </CookieConsent>

    )
}

export default ConfirmCookies;