import React from 'react';
import { Link } from 'react-router-dom';
import { ArrowRightIcon } from '@heroicons/react/solid';

function Footer() {
    const handleSubmit = e => {
        e.preventDefault();
        alert('Newsletter signup form has been submitted');
    }

    return (
        <div className='border-t border-mediumgray mt-16 lg:mt-24 px-10 pt-10 pb-5 lg:pb-16 flex flex-col lg:flex-row gap-x-8 font-quicksand text-sm'>
            <div className='hidden lg:block w-3/12'>
                <p className='text-2xl'>T2 Codes</p>
                <p className='mt-1'>©T 2 Codes, Poland</p>
            </div>
            <div className='w-full lg:w-5/12 flex'>
                <div className='w-3/6'>
                    <span className='font-semibold'>T2 Codes</span>
                    <ul>
                        <li className='mt-2'>
                            <Link to='/' className='hover:underline'>Home</Link>
                        </li>
                        <li className='mt-2'>
                            <Link to='/support' className='hover:underline'>Support</Link>
                        </li>
                        <li className='mt-2'>
                            <Link to='/login' className='hover:underline'>Login</Link>
                        </li>
                        <li className='mt-2'>
                            <Link to='/register' className='hover:underline'>Register</Link>
                        </li>
                    </ul>
                </div>
                <div className='w-3/6'>
                    <span className='font-semibold'>Policies</span>
                    <ul>
                        <li className='mt-2'>
                            <Link to='#' className='hover:underline'>Privacy Policy</Link>
                        </li>
                        <li className='mt-2'>
                            <Link to='#' className='hover:underline'>Terms of Use and Sales</Link>
                        </li>
                        <li className='mt-2'>
                            <Link to='#' className='hover:underline'>Refund Policy</Link>
                        </li>
                    </ul>
                </div>
            </div>
            <div className='w-full md:w-4/6 lg:w-4/12 my-10 lg:my-0 mx-auto text-center'>
                <span className='font-semibold'>Join Our Newsletter</span>
                <form className='mt-2 relative' onSubmit={handleSubmit}>
                    <input
                        placeholder='Email Address'
                        className='w-full h-10 pl-5 pr-10 rounded-lg bg-lightgray focus:outline-none'
                    />
                    <button className='bg-transparent absolute top-3 right-3 w-4 h-4'>
                        <ArrowRightIcon />
                    </button>
                </form>
            </div>
            <div className='lg:hidden text-center text-xs'>
                ©T 2 Codes, Poland
            </div>
        </div>
    );
}

export default Footer;