import './App.css';
import { HashRouter as Router, Routes, Route } from "react-router-dom"
import { HomePage, LoginPage, RegisterPage, SupportPage } from './components';

function App() {
    return (
        <Router>
            <Routes>
                <Route
                    path='/'
                    element={<HomePage />}
                />
                <Route
                    path='/login'
                    element={<LoginPage />}
                />
                <Route
                    path='/register'
                    element={<RegisterPage />}
                />
                <Route
                    path='/support'
                    element={<SupportPage />}
                />
            </Routes>
        </Router>
    );
}

export default App;
