import React from 'react';

function IconInputField(props) {
    return (
        <div className='relative'>
            <input
                placeholder={props.placeholder}
                value={props.value}
                onChange={e => props.setValue(e.target.value)}
                className='w-full h-10 px-10 rounded-lg bg-lightgray focus:outline-none font-quicksand text-sm'
                type={ props.showText === false ? 'password' : 'text' }
            />
            <div className='bg-transparent absolute top-3 left-3 text-darkgray w-4 h-4'>
                { props.Icon }
            </div>
            { props.children }
        </div>
    );
}

export default IconInputField;