import { React, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import IconInputField from './IconInputField';
import { MailIcon, ShieldCheckIcon, EyeIcon, EyeOffIcon } from '@heroicons/react/outline';

function LoginPage() {

    const navigate = useNavigate();

    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [emailError, setEmailError] = useState('');
    const [passwordError, setPasswordError] = useState('');
    const [showPassword, setShowPassword] = useState(false);

    const toggleShowPassword = () => {
        setShowPassword(!showPassword);
    }

    const validateEmail = email => {
        return String(email)
            .toLowerCase()
            .match(
                /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
            );
    };

    const validateUserInput = () => {
        let errorFound = false;

        if (email === '' || !validateEmail(email)) {
            setEmailError('Email is invalid');
            errorFound = true;
        } else {
            setEmailError('');
        }

        if (password === '') {
            setPasswordError('Password is invalid');
            errorFound = true;
        } else {
            setPasswordError('');
        }

        return !errorFound;
    }

    const handleSubmit = e => {
        e.preventDefault();

        if (validateUserInput()) {
            console.log({ email, password });
            navigate('/');
        }
    }

    return (
        <div className='min-h-screen bg-white sm:bg-lightgray py-10 sm:py-32 font-quicksand'>
            <div className='mx-auto w-[360px] bg-white rounded-lg sm:shadow-lg p-10 text-center'>
                <div className='text-darkgray'>
                    <p className='text-2xl'><Link to='/'>T2 Codes</Link></p>
                    <p className='mt-5 text-base'>Login into your account</p>
                </div>
                <form className='mt-5 flex flex-col gap-y-3' onSubmit={handleSubmit}>
                    <div>
                        <p className='text-sm text-left text-[#ff0033] mb-1'>
                            {emailError}
                        </p>
                        <IconInputField
                            placeholder="yours@example.com"
                            Icon={<MailIcon />}
                            value={email}
                            setValue={setEmail}
                        />
                    </div>
                    <div>
                        <p className='text-sm text-left text-[#ff0033] mb-1'>
                            {passwordError}
                        </p>
                        <IconInputField
                            placeholder="Enter Password"
                            Icon={<ShieldCheckIcon />}
                            value={password}
                            showText={showPassword}
                            setValue={setPassword}
                        >
                            <button
                                onClick={toggleShowPassword}
                                className='bg-transparent absolute top-3 right-3 text-darkgray w-4 h-4'
                            >
                                {showPassword ? <EyeOffIcon /> : <EyeIcon />}
                            </button>
                        </IconInputField>
                    </div>
                    <button className='w-full bg-rose hover:bg-darkrose text-white py-2 rounded-lg'>
                        Login
                    </button>
                </form>
                <p className='text-sm mt-5 text-rose hover:underline'>
                    <Link to='/register'>Don't have an account? Register</Link>
                </p>
            </div>
        </div>
    )
}

export default LoginPage;