import React from "react";

function BenefitCard(props) {
    const tmp = 'benefit-1.png';
    return (
        <div className={"mt-16 flex flex-col gap-x-12 gap-y-5 " + (props.textOnLeft ? 'lg:flex-row' : 'lg:flex-row-reverse')}>
            <div className="flex align-middle w-full lg:w-3/6 text-center lg:text-left">
                <div className="my-auto">
                    <p className="text-sm font-semibold font-quicksand text-rose tracking-widest uppercase">
                        {props.subheading}
                    </p>
                    <h2 className="mt-3 text-3xl lg:text-4xl xl:text-[2.8rem] xl:leading-tight capitalize max-w">
                        {props.heading}
                    </h2>
                    <p className="mt-3 font-quicksand text-xl leading-8">
                        {props.text}
                    </p>
                </div>
            </div>
            <div className="w-full lg:w-3/6 mt-8 lg:mt-0">
                <div className="sm:w-4/6 lg:w-full mx-auto">
                    { props.image }
                </div>
            </div>
        </div>
    );
}

export default BenefitCard;