import React from 'react';
import HowWeHelpCard from './HowWeHelpCard';
import { CodeIcon, UserGroupIcon, SparklesIcon } from '@heroicons/react/outline';

function HowWeHelpSection() {
    return (
        <div className='mt-16 lg:mt-32 text-center'>
            <h2 className='text-3xl lg:text-4xl'>How We Help</h2>
            <div className='w-24 h-0.5 bg-rose rounded-full mx-auto mt-5'></div>
            <div className='mt-10 flex flex-col md:flex-row gap-5'>
                <HowWeHelpCard 
                    logo={<CodeIcon />} 
                    text={<>We make you <br/>an app</>}
                />
                <HowWeHelpCard 
                    logo={<UserGroupIcon />} 
                    text={<>We hire you <br/>a team</>}
                />
                <HowWeHelpCard 
                    logo={<SparklesIcon />} 
                    text={<>We train you <br/>and your team</>}
                />
            </div>
        </div>
    );
}

export default HowWeHelpSection;