import React from 'react';
import { Link } from 'react-router-dom'

function CallToActionSection() {
    return (
        <div className='bg-lightgray mt-16 lg:mt-24 text-center p-10 lg:py-16 rounded-lg'>
            <h3 className='text-3xl lg:text-4xl xl:text-[2.8rem] capitalize'>
                Get Started Today
            </h3>
            <p className='font-quicksand mt-5 text-xl leading-8'>
                Take the first step towards turning your big idea into a reality today.
            </p>
            <Link to='/register'>
                <button className='mt-5 px-10 py-2 bg-rose hover:bg-darkrose text-white rounded-full'>
                    I'm in!
                </button>
            </Link>
        </div>
    );
}

export default CallToActionSection;