import React from "react";
import BenefitCard from "./BenefitCard";

function BenefitSection() {
    return (
        <div className="lg:pt-8">
            <BenefitCard 
                textOnLeft={false} 
                subheading='Endless possibilities' 
                heading='E-Commerce Apps, Order Booking Systems Or A Website For Your Local Business' 
                text='We bring your ideas to life, however unique they may be'
                image={
                    <img src={require('./../assets/benefit-1.png')} />
                }
            />
            <BenefitCard 
                textOnLeft={true} 
                subheading='Hassel free' 
                heading='Little To No Maintainance' 
                text='With our top-notch products and training, you and your team can maintain your product with little to no external help'
                image={
                    <img src={require('./../assets/benefit-2.png')} />
                }
            />
            <BenefitCard 
                textOnLeft={false} 
                subheading="We're there for you" 
                heading='24/7 Customer Support' 
                text='With our outstanding team, we strive to clear all your doubts and questions to make this journey seamless for you'
                image={
                    <img src={require('./../assets/benefit-3.png')} />
                }
            />
            <BenefitCard 
                textOnLeft={true} 
                subheading='Authentic content' 
                heading='Apps In Your Native Language' 
                text='With our diverse team of developers, we provide apps in your own native language without any funny auto-translations'
                image={
                    <img src={require('./../assets/benefit-4.png')} />
                }
            />
        </div>
    );
}

export default BenefitSection;